import { doc, onSnapshot } from "firebase/firestore";
import { writable } from "svelte/store";
import { userStore } from "../auth/user";
import { getDb } from "../db";
const internalCreditStore = writable(undefined);
export const creditStore = { subscribe: internalCreditStore.subscribe };
let unsub = () => { };
userStore.subscribe(user => {
    unsub();
    if (!user) {
        unsub = () => { };
        return;
    }
    const docRef = doc(getDb(), "users", user.uid);
    unsub = onSnapshot(docRef, snapshot => {
        const credit = snapshot.get("secondsCredit");
        internalCreditStore.set(credit);
    });
});
export function toCreditString(credit) {
    if (credit === undefined)
        return "Loading";
    if (credit < 0)
        return "Overdrawn";
    if (credit === 0)
        return "None";
    const hours = Math.floor(credit / 3600);
    const minutes = Math.floor((credit % 3600) / 60);
    const seconds = credit % 60;
    const hourString = hours.toString().padStart(2, "0");
    const minuteString = minutes.toString().padStart(2, "0");
    const secondString = seconds.toString().padStart(2, "0");
    return `${hourString}:${minuteString}:${secondString}`;
}
