import { userStore } from "./auth/user";
let user = undefined;
userStore.subscribe(state => user = state);
function assertUser() {
    if (!user)
        throw new Error("User is undefined when calling authenticated api");
    return user;
}
export async function uploadFile(file, name) {
    return assertUser().getIdToken()
        .then(idToken => fetch(`https://europe-west2-${process.env["PROJECT_ID"]}.cloudfunctions.net/upload`, {
        method: "post",
        headers: {
            "Authorization": `Bearer ${idToken}`,
            "Content-Type": "application/json"
        },
        body: JSON.stringify({
            name: name
        })
    }))
        .then(async (res) => {
        if (res.ok)
            return res;
        const text = await res.text();
        throw new Error("Request was rejected: " + text);
    })
        .then(res => res.text())
        .then(url => fetch(url, {
        method: "put",
        headers: {
            "Content-Type": "application/octet-stream"
        },
        body: file
    }));
}
export async function addCredit(mins) {
    return assertUser().getIdToken()
        .then(idToken => fetch(`https://europe-west2-${process.env["PROJECT_ID"]}.cloudfunctions.net/create_checkout`, {
        method: "post",
        headers: {
            "Authorization": `Bearer ${idToken}`,
            "Content-Type": "application/json"
        },
        body: JSON.stringify({ mins })
    }))
        .then(async (res) => {
        if (res.ok)
            return res;
        const text = await res.text();
        throw new Error("Request was rejected: " + text);
    })
        .then(res => res.text())
        .then(url => { window.location.href = url; });
}
